/**
 * Main styles
 */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap");
.container, .card {
  overflow: hidden;
}

html {
  font-size: 100%;
}

*,
::before,
::after {
  box-sizing: border-box;
}

[hidden],
[hidden] * {
  display: none !important;
}

body {
  min-height: 100vh;
  margin: 0;
  font-family: "Inter", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.2;
  color: #000;
  background-color: #fff;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.75rem;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 3.75rem;
}

p {
  font-size: 0.9375rem;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5625rem;
  margin-top: 0;
  margin-bottom: 2rem;
}

.container {
  width: 90vw;
  max-width: 75rem;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem;
  padding: 0;
}

header {
  height: 8.75rem;
  margin-top: 2.5rem;
  color: #333;
  background: #FFF;
  user-select: none;
}
header a {
  color: #333;
}
header a:hover, header a:active {
  color: #333;
}
header nav {
  text-align: center;
  font-size: 1.0625rem;
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.3125rem;
}
header nav a {
  padding: 0.75rem 0;
  margin-right: 2rem;
  position: relative;
}
header nav a:last-child {
  margin-right: 0;
}
header nav a:before {
  position: absolute;
  content: "";
  bottom: 0.25rem;
  left: 50%;
  right: 50%;
  height: 3px;
  background: #333;
  transition: 0.75s;
}
header nav a:hover, header nav a:active {
  font-weight: 700;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2509803922);
}
header nav a:hover:before, header nav a:active:before {
  left: 25%;
  right: 25%;
}
@media (max-width: 768px) {
  header nav {
    display: none;
  }
}
header .logo {
  margin-right: auto;
  height: 2.75rem;
  line-height: 2.75rem;
  overflow: hidden;
}
header .nav-btn {
  width: 2.5rem;
  height: 2.5rem;
}
@media (min-width: 768px) {
  header .nav-btn {
    display: none;
  }
}

.picture {
  overflow: hidden;
  width: 100%;
}
.picture img {
  width: 100%;
  height: 20.25rem;
  object-position: center;
  object-fit: cover;
}

.article {
  color: #000;
  padding-bottom: 2rem;
}
.article .heading {
  color: #333;
}
@media (min-width: 768px) {
  .article {
    width: 49.5625rem;
    margin-bottom: 0;
  }
}

.btn {
  border-radius: 50px;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.0625rem;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5625rem;
  padding: 0.5rem 1.5rem;
  box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.2509803922);
}
.btn:hover, .btn:active {
  box-shadow: inset 0 0.25rem 0.25rem rgba(0, 0, 0, 0.2509803922);
}
.btn.btn-pink {
  border: 1px solid #BBACC1;
  color: #F1DEDE;
  background-color: #BBACC1;
}
.btn.btn-pink:hover, .btn.btn-pink:active {
  color: #FFF;
  background: #BBACC1;
  border: 1px solid #333;
}

.card .card-img img {
  object-position: center;
  object-fit: cover;
}
.card .card-header {
  font-size: 1.9375rem;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5625rem;
  color: #333;
  margin-bottom: 0.75rem;
}
.card .card-title {
  font-size: 1.1875rem;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5625rem;
  text-transform: capitalize;
  color: #BBACC1;
}
.card .card-title:hover, .card .card-title:active {
  color: #F1DEDE;
}

.news {
  padding: 2rem;
  background-color: #333;
  color: #FFF;
}
.news .news-heading {
  font-size: 2.5rem;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 3.75rem;
  color: #BBACC1;
}
.news .card {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.news .card .card-title {
  color: #F1DEDE;
  text-transform: capitalize;
}
.news .card .card-title:hover, .news .card .card-title:active {
  color: #FFF;
}
.news .card .card-header {
  font-size: 1.1875rem;
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.5625rem;
  color: #F1DEDE;
  text-transform: capitalize;
}
.news .card .card-header:hover, .news .card .card-header:active {
  color: #FFF;
}
.news .card:not(:last-of-type)::after {
  content: "";
  height: 1px;
  width: 100%;
  background-color: #FFF;
  display: inline-block;
  padding-left: auto;
  padding-right: auto;
}

@media (min-width: 768px) {
  #main .news {
    margin-left: 1rem;
  }
}

.container,
.card {
  display: flex;
}

#main {
  flex-direction: column;
}
@media (min-width: 768px) {
  #main {
    flex-direction: row;
  }
}

.news-inner {
  display: flex;
  flex-direction: column;
}
.news-inner .news-heading {
  margin-bottom: auto;
}
.news-inner .card {
  flex-direction: column;
}

.related {
  flex-direction: column;
}
@media (min-width: 992px) {
  .related {
    flex-direction: row;
    justify-content: space-between;
  }
}

header {
  align-items: center;
}
